import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";

//import completedStyle from "assets/jss/material-kit-react/views/componentsSections/completedStyle.jsx";
import productStyle from "assets/jss/material-kit-react/views/landingPageSections/productStyle.jsx";

class SectionHome extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.section}>
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={8}>
              <h2 className={classes.title}>MIELE E PRODOTTI DELL'ALVEARE</h2>
              <h4 className={classes.description}>
                Il nostro miele viene estratto manualmente e non viene sottoposto a nessun altro tipo di lavorazione, per questo cristallizza naturalmente.

                I nostri prodotti trasmettono i profumi tipici dei nostri territori irpini, a partire dal miele di sulla, varietà floreale primaria della nostra zona caratterizzata da ampi pascoli e terreni seminati per fienaggione, al castagno prodotto dalle fioriture degli alberi caratteristici delle nostre zone.
              </h4>
            </GridItem>
            <GridItem xs={12} sm={12} md={8}>
              <h2 className={classes.title}>LA CRISTALLIZZAZIONE DEL MIELE: un fenomeno naturale</h2>
              <h4 className={classes.description}>
              La cristallizzazione è un processo naturale che interessa il miele ed è un segno della sua genuinità.

              La cristallizzazione è legata alla alla composizione chimica del miele, e ogni tipologia di miele ha diversi tempi e modalità di 
              cristallizzazione.<br />
              
              La scelta tra liquido e cristallizzato è una questione di preferenze personali; se però si vogliono privilegiare i prodotti più integri 
              bisogna ricordare che, al di fuori della stagione produttiva, i soli mieli che si mantengono stabilmente liquidi sono l’acacia, 
              il castagno e le melate. Negli altri la presentazione allo stato liquido dipende da un trattamento termico di rifusione ed è noto 
              che il riscaldamento produce una perdita di alcune caratteristiche naturali del miele.<br />.
              
              Il miele cristallizzato acquistato può essere riportato eventualmente allo stato liquido per mezzo di un opportuno riscaldamento (a bagnomaria a temperatura non superiore a 40′ C).
              Ma può essere ancora più interessante abituarsi all’uso di miele cristallizzato: si scoprirà allora che questa forma fisica rende forse
               il miele meno comodo da prelevare e da sciogliere, ma molto più facile da maneggiare, in quanto non cola, e molto più piacevole da
                consumare poiché viene percepito meno dolce e più fresco al palato.
              </h4>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(productStyle)(SectionHome);
